"use client";

import React, { useState } from "react";
import Footer from "@/components/footer";
import Sidebar from "@/components/sidebar";
import InfantList from "./infantList";
import { useProtectedRoutesApi } from "@/libraries/API/ProtectedRoute/secureRoutes";
import { useQueryClient } from "@tanstack/react-query";
import { Input } from "@/components/ui/input";
import { Button } from "@/components/ui/button"; // Importing the ShadCN button

const InfantManagement = () => {
  const { createInfant } = useProtectedRoutesApi();
  const queryClient = useQueryClient();
  const [id, setId] = useState("");
  const [image, setImage] = useState("");
  const [fullname, setFullname] = useState("Emman Mota");
  const [month, setMonth] = useState("12");
  const [day, setDay] = useState("30");
  const [year, setYear] = useState("2024");
  const [purok, setPurok] = useState("Purok 2");
  const [baranggay, setBaranggay] = useState("Calzada");
  const [municipality, setMunicipality] = useState("Guinobatan");
  const [province, setProvince] = useState("Albay");
  const [place_of_birth, setPlaceOfBirth] = useState("Guinobatan");
  const [height, setHeight] = useState("167");
  const [gender, setGender] = useState("Male");
  const [weight, setWeight] = useState("5");
  const [mothers_name, setMothersName] = useState("Rose");
  const [fathers_name, setFathersName] = useState("Henry");
  const [health_center, setHealthCenter] = useState("Guinobatan");
  const [family_no, setFamilyNo] = useState("2");

  return (
    <div className="grid grid-cols-[250px_1fr] grid-rows-[1fr_auto] min-h-screen">
      <Sidebar />

      <main className="p-8 sm:p-20 font-[family-name:var(--font-geist-sans)] flex flex-col h-[calc(100vh-4rem)] space-y-8 relative">
        {/* Top Bar with Add Infant Button */}
        <div className="flex justify-end">
          <Button
            className="px-4 py-2"
            onClick={() => alert("Add Infant Clicked!")}
          >
            Add Infant
          </Button>
        </div>

        {/* Input Field */}
        <Input
          value={purok}
          onChange={(e) => setPurok(e.target.value)}
          placeholder="Purok"
        />

        {/* Infant list display */}
        <InfantList />
      </main>

      <Footer />
    </div>
  );
};

export default InfantManagement;
